.app-wrapper {
  display: grid;
  width: 1200px;
  margin: 0 auto;
  grid-template-areas: "header header header" "nav content content";

  grid-template-rows: 42px 1fr;
  grid-template-columns: 1fr 10fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  grid-area: content;
  background-color: lightcyan;
  border-radius: 10px;
}
